
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.canvas-container {
  position: relative;
  width: 280px;
  height: 280px;
  margin: 0 auto;
}
canvas {
  border: 1px solid #DDD;
}
.clear {
  background: #2c3e50;
  color: #fff;
  border-radius: 4px;
  width: 100px;
  margin: 20px auto;
  line-height: 30px;
  font-size: 18px;
  cursor: pointer;
}
